ul li {
  list-style: none;
}


#wrap {
  width: 40%;
  text-align: center;
  border: 3px solid #333;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 15px;
  overflow: hidden;
  font-family: 'NanumGothic', 'Noto Sans KR', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  line-height: 1.5;
  margin: 0 15px 15px;
  height: 72vh;
}

#wrap h2 {
  margin: 15px 0 0;
}

#wrap h2 a {
  font-family: "Roboto", sans-serif;
  text-decoration: none;
  color: #3944bc;
  font-size: 42px;
}

#wrap p {
  margin: 15px 0;
  padding: 0 15px;
  font-size: 20px;
}

#wrap img {
  width: 28%;
  margin-top: 15px;
}

#wrap ul {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 15px 0;
}

#wrap ul li {
  margin: 0 15px;
  padding: 10px 20px;
  border-radius: 25px;
  background: #a0ced9;
  color: #fff;
  font-weight: bold;
  letter-spacing: 1px;
  font-size: 18px;
  font-family: "Noto Sans", sans-serif;
}